import React from "react";
import { Result, Button } from 'antd';

const View = (props) => {
  const { title, status, msg, onClose, onGoto } = props

  return (
    <Result
      status={status ? "success" : 'error'}
      title={title ? title : (status ? "操作成功!" : '操作失败!')}
      subTitle={msg}
      extra={[
        onClose ? <Button type="primary" key="close" onClick={() => onClose()}>
          关闭页面
        </Button> : null,
        onGoto ? <Button key="again" onClick={() => onGoto()}>继续操作</Button> : null,
      ]}
    />
  );
}
export default View